import PropTypes from 'prop-types';
import { useRef, useEffect } from 'react';
import {PageTitle} from '../../components';
import { PAGE_TITLE_CONSTANT } from '../../constants';
import {ImageCards} from '../../container';
import styles from './GuestPage.module.scss';

function GuestPage(props) {
  const { details } = props;
  const guestPageRef = useRef(null);
  useEffect(()=>{
    guestPageRef.current.scrollIntoView({block: 'start'});
  });

  return (
    <div ref={guestPageRef} className={styles.guestContainer}>
      <PageTitle label={PAGE_TITLE_CONSTANT.GUEST} />
      <ImageCards details={details} />
    </div>
  );
}

GuestPage.propTypes = {
  details: PropTypes.array,
};
export default GuestPage;
